export default function (text) {
    switch(text) {
          case 'normal':
              return 'grey';
          case 'fighting':
              return 'rgb(165,82,57)';
          case 'flying':
              return 'rgb(156, 173, 245)';
          case 'poison':
              return 'rgb(181, 90, 165)';
          case 'rock':
              return 'rgb(190, 165, 90)';
          case 'ground':
              return 'rgb(175, 145, 65)';
          case 'dragon':
              return 'rgb(108,93,176)';
          case 'ice':
              return 'rgb(90, 200, 230)';
          case 'water':
              return 'rgb(55, 155, 255)';
          case 'fire':
              return 'rgb(250, 80, 50)';
          case 'grass': 
              return 'rgb(125, 205, 85)';
          case 'bug':
              return 'rgb(175, 190, 30)';
          case 'psychic':
              return 'rgb(255, 115, 165)';
          case 'electric':
              return 'rgb(255, 215, 0)';
          case 'ghost':
              return 'rgb(115, 85, 230)';
          case 'dark':
              return 'rgb(115, 90, 75)';
          case 'steel':
              return 'silver';
          case 'fairy':
              return 'rgb(245, 180, 245)';
          
      }
}