import {
  SET_POKEMON_ID,
  SET_CURRENT_POKEMON_DATA,
  SAVE_CURRENT_POKEMON_DATA_TO_ARRAY,
  SAVE_ARRAY_TO_JSON,
  SET_NEW_TM_LEARNSETS,
  SET_POKEMON_DATA_ARRAY,
  SET_POKEMON_DATA_ARRAY_FROM_JSON,
  UPLOAD_INITIAL_JSON,
  SET_MUTATED_CURRENT_POKEMON_DATA
} from './mutationTypes'

export default {
  actions: {
    setPokemonId ({ commit }, id) {
      commit(SET_POKEMON_ID, id)
    },
    setCurrentPokemonData ({ commit }) {
      commit(SET_CURRENT_POKEMON_DATA)
    },
    setMutatedCurrentPokemonData ({ commit }, data) {
      commit(SET_MUTATED_CURRENT_POKEMON_DATA, data)
    },
    saveCurrentPokemonDataToArray ({ commit }) {
      commit(SAVE_CURRENT_POKEMON_DATA_TO_ARRAY)
    },
    setNewTMLearnsets({ commit }, newTMLearnsets) {
      commit(SET_NEW_TM_LEARNSETS, newTMLearnsets)
    },
    saveArrayToJson ({ commit }) {
      commit(SAVE_ARRAY_TO_JSON)
    },
    setPokemonDataArray ({ commit }, array) {
      commit(SET_POKEMON_DATA_ARRAY, array)
    },
    setPokemonDataArrayFromJson ({ commit }) {
      const dataArray = JSON.parse(localStorage.getItem('pokemonDataArray'))

      if(!Array.isArray(dataArray) || dataArray.length === 0) {
        commit(SET_POKEMON_DATA_ARRAY, [])
        throw new Error('No data in localStorage')
      }

      commit(SET_POKEMON_DATA_ARRAY_FROM_JSON, dataArray)
    },
    uploadJSON({commit}, data) {
      commit(UPLOAD_INITIAL_JSON, data)
    }
  }
}