<template>
  <ALayout>
    <ALayoutHeader>
      <div class="logo"></div>
    </ALayoutHeader>
    <AModal v-model:visible="firstLaunch" title="Upload your PersonalTable.JSON" footer="">
          <FileUploadModal :ok="handleOk"/>
    </AModal>
    <ALayoutContent>
      <label for="selectPokemon">Current Pokemon: </label>
      <ASelect
            id="selectPokemon"
            v-model="currentValue"
            :options="options"
            show-search
            placeholder="Please select a Pokemon"
            :filter-option="filterOption"
            @change="handleChange">
      </ASelect>
      <ATabs v-model:activeKey="activeKey">
        <ATabPane key="1" tab="TM Learnset Editor">
          <TMEditor></TMEditor>
        </ATabPane>
        <ATabPane key="2" tab="Base Stats Editor">
          <BaseEditor></BaseEditor>
        </ATabPane>
        <ATabPane key="3" tab="Learnset Editor" disabled>
          
        </ATabPane>
      </ATabs>
       <AButton type="primary" @click="downloadJSON">Finished? Download your modified JSON file to repack.</AButton>
    </ALayoutContent>
  <ALayoutFooter>This application is in beta. Please refer all bug findings to the Luminescent Version Romhack Discord.</ALayoutFooter>
  </ALayout>
</template>

<script>
import TMEditor from './components/TMEditor.vue'
import BaseEditor from './components/BaseEditor.vue'
import FileUploadModal from './components/FileUploadModal.vue'
import { defineComponent, ref } from 'vue'
import {mapActions, mapGetters} from 'vuex'
import getPokemonName from './utils/getPokemonName';
import { notification } from 'ant-design-vue'

const selectValues = [{value: '', label: ''}]

export default defineComponent({
  name: 'App',
  setup() {
    return {
      activeKey: ref('1'),
    };
  },
  data() {
    return {
      firstLaunch: true,
      currentValue: {label: selectValues[0].label, value: selectValues[0].value},
      options: [...selectValues]
    }
  },
  watch: {
    getCurrentJSON: {
      handler(newValue) {
        if(!this.firstLaunch) {
            if (Array.isArray(newValue.Personal) && newValue.Personal.length > 0) {
                this.options = newValue.Personal.map(p => ({value: p.monsno,label: getPokemonName(p.monsno)})).filter(p => p.value !== 0);
            } else {
              notification.error({
                message: 'Error',
                description: 'No Personal Table.JSON found. Please upload one.',
              });
            }
        }
      },
      immediate: true
    }
  },
  computed: mapGetters('machines', ['getCurrentJSON']),
  methods: {
    ...mapActions('machines', ['uploadJSON', 'setPokemonId', 'setCurrentPokemonData', 'saveCurrentPokemonDataToArray', 'saveArrayToJson']),
    filterOption(input, option) {
      return option.label.toLowerCase().indexOf(input.toLowerCase()) >= 0;
    },
    handleChange(value) {
        this.saveCurrentPokemonDataToArray();

        this.setPokemonId(value);
        this.setCurrentPokemonData();
    },
    handleOk(jsonData) {
      this.uploadJSON(jsonData);
      this.firstLaunch = false;
    },
    downloadJSON() {
        if(!(this.getCurrentPokemonId === 0 || this.getCurrentPokemonId === undefined)) {
            this.saveCurrentPokemonDataToArray();
        }
        
        this.saveArrayToJson();
        const json = this.$store.state.machines.pokemonDataArrayJson
        const blob = new Blob([JSON.stringify(json, null , 2)], {type: "application/json"});
        const url = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', 'PersonalTable.json');
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
    }
  },
  components: {
    TMEditor,
    BaseEditor,
    FileUploadModal
  }
})
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>
