export default {
  getters: {
    getCurrentPokemonId: state => state.currentPokemonId,
    getCurrentPokemon: state => state.currentPokemonData,
    getCurrentDataArray: state => state.pokemonDataArray,
    getCurrentJSON: state => state.pokemonDataArrayJson,
    getTMListOfPokemon: state => {
      const m1 = state.currentPokemonData.machine1;
      const m2 = state.currentPokemonData.machine2;
      const m3 = state.currentPokemonData.machine3;
      const m4 = state.currentPokemonData.machine4;
      return {m1: m1, m2: m2, m3: m3, m4: m4};
    },
  }
}