import _ from 'lodash'
import {
    SET_POKEMON_ID,
    SET_CURRENT_POKEMON_DATA,
    SAVE_CURRENT_POKEMON_DATA_TO_ARRAY,
    SAVE_ARRAY_TO_JSON,
    SET_POKEMON_DATA_ARRAY,
    SET_POKEMON_DATA_ARRAY_FROM_JSON,
    SET_NEW_TM_LEARNSETS,
    SET_MUTATED_CURRENT_POKEMON_DATA,
    UPLOAD_INITIAL_JSON
 } from './mutationTypes'



export default {
  mutations : {
    [SET_POKEMON_ID] (state, pokemonId) {
      if(typeof pokemonId !== 'number' || pokemonId < 1 || isNaN(pokemonId)) {
        throw new Error(`Invalid pokemonId: ${pokemonId}`)
      }
      
      state.currentPokemonId = pokemonId
    },
    [SET_CURRENT_POKEMON_DATA] (state) {
      state.currentPokemonData = 
        state.pokemonDataArray.find(pokemon => pokemon.monsno === state.currentPokemonId)
    },
    [SET_MUTATED_CURRENT_POKEMON_DATA] (state, payload) {
      state.currentPokemonData = _.merge(state.currentPokemonData, payload)
    },
    [SAVE_CURRENT_POKEMON_DATA_TO_ARRAY] (state) {
      if(state.currentPokemonId === 0) {
          return
      }
      
      const currentPokemonArrayIndex = 
        state.pokemonDataArray.findIndex(pokemon => pokemon.id === state.currentPokemonData.id)
      if(currentPokemonArrayIndex === -1) {
        throw new Error('No current pokemon data in array')
      }
      
      const currentPokemonData = _.cloneDeep(state.currentPokemonData)
      state.pokemonDataArray[currentPokemonArrayIndex] = currentPokemonData
    },
    [SET_NEW_TM_LEARNSETS](state, payload) {
        const {machine1, machine2, machine3, machine4} = payload
        state.currentPokemonData.machine1 = machine1
        state.currentPokemonData.machine2 = machine2
        state.currentPokemonData.machine3 = machine3
        state.currentPokemonData.machine4 = machine4
    },
    [SAVE_ARRAY_TO_JSON] (state) {
      const pokemonDataArray = _.cloneDeep(state.pokemonDataArray)
      state.pokemonDataArrayJson.Personal = pokemonDataArray
    },
    [SET_POKEMON_DATA_ARRAY] (state, pokemonDataArray) {
      if(!Array.isArray(pokemonDataArray)) {
        throw new Error('Invalid pokemonDataArray')
      }
      const clonedPokemonDataArray = _.cloneDeep(pokemonDataArray)
      state.pokemonDataArray = clonedPokemonDataArray
    },
    [SET_POKEMON_DATA_ARRAY_FROM_JSON] (state, pokemonDataArrayJson) {
      if(!pokemonDataArrayJson) {
        throw new Error('Invalid pokemonDataArrayJson')
      }
      
      const clonedPokemonDataArray = _.cloneDeep(pokemonDataArrayJson.Personal)
      state.pokemonDataArray = clonedPokemonDataArray
    },
    [UPLOAD_INITIAL_JSON] (state, pokemonDataArrayJson) {
        if(!pokemonDataArrayJson) {
          throw new Error('Invalid pokemonDataArrayJson')
        }
        
        state.pokemonDataArrayJson = _.cloneDeep(pokemonDataArrayJson)
        state.pokemonDataArray = _.cloneDeep(pokemonDataArrayJson.Personal)
    }
  }
}