<template>
    <ALayout>
        <ATable :columns="columns" :data-source="data">
            <template #id="{ text }">
            <a>{{ text }}</a>
            </template>
            <template #customTitle>
            <span>
                TM Number
            </span>
            </template>
            <template #type="{ text }">
            <span>
                <ATag
                :key="text"
                :color="text === 'TBD' ? 'grey' : getColor(text)"
                >
                {{ text.toUpperCase() }}
                </ATag>
            </span>
            </template>
            <template #enabled="{ record }">
            <span>
                <ACheckbox v-model:checked="record.enabled" @change="updatePokemonFlags"></ACheckbox>
            </span>
            </template>
        </ATable>
    </ALayout>
  
</template>
<script>
import TMJson from '../assets/json/tm.json';
import TMTypes from '../assets/json/tmTypes.json';
import getColor from '../utils/getColor';
import { mapGetters, mapActions } from 'vuex'
import {bin2dec, dec2bin, addMissingBits} from '../utils/binary.js';
import getPokemonName from '../utils/getPokemonName';
import { notification } from 'ant-design-vue';

const columns = [
  {
    dataIndex: 'id',
    key: 'id',
    slots: {
      title: 'customTitle',
      customRender: 'id',
    },
  },
  {
    title: 'Name',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Type',
    key: 'type',
    dataIndex: 'type',
    slots: {
      customRender: 'type',
    },
  },
  {
    title: 'Enabled',
    key: 'enabled',
    slots: {
      customRender: 'enabled',
    },
  },
];
const data =  TMJson.map((tm, i) => {
    return {
        key: i,
        id: tm.id,
        name: tm.text,
        type: TMTypes[i].type,
        enabled: false,
    };
});

function convertToTMList({m1, m2, m3, m4}) {
    const selectedPokemonTMLearnset1 = dec2bin(Number(m1) || 0); //Max 32
    const selectedPokemonTMLearnset2 = dec2bin(Number(m2) || 0); //Max 32
    const selectedPokemonTMLearnset3 = dec2bin(Number(m3) || 0); //Max 32
    const selectedPokemonTMLearnset4 = dec2bin(Number(m4) || 0); //Max 4

    const pokemonTmLearnset1 = addMissingBits(selectedPokemonTMLearnset1.split("").map(bit => Number(bit))).reverse();
    const pokemonTmLearnset2 = addMissingBits(selectedPokemonTMLearnset2.split("").map(bit => Number(bit))).reverse();
    const pokemonTmLearnset3 = addMissingBits(selectedPokemonTMLearnset3.split("").map(bit => Number(bit))).reverse();
    const pokemonTmLearnset4 = addMissingBits(selectedPokemonTMLearnset4.split("").map(bit => Number(bit))).reverse();
    const tmList = [...pokemonTmLearnset1, ...pokemonTmLearnset2, ...pokemonTmLearnset3, ...pokemonTmLearnset4];
    const defaultList = new Array(100).fill(0);
    
    for(let i = 0; i < tmList.length; i++) {
        if(tmList[i] === 1) {
            defaultList[i] = 1;
        }
    }
    return defaultList;
}

export default {
  data() {
      return {
        data,
        columns
      }
  },
  computed: mapGetters('machines', ['getTMListOfPokemon', 'getCurrentPokemonId']),
  methods: {
    ...mapActions('machines', ['setNewTMLearnsets', 'saveCurrentPokemonDataToArray']),
    getColor: getColor,
    updatePokemonFlags() {
        if(this.getCurrentPokemonId === 0 || this.getCurrentPokemonId === undefined) {
            notification.error({
                message: 'Please select a pokemon',
                description: '',
                duration: 2,
            });
            return;
        }

        const learnsets = this.createBinaryString();
        this.setNewTMLearnsets(learnsets);
        this.saveCurrentPokemonDataToArray();
        notification.success({
            message: 'Success!',
            description: `TM learnsets updated for ${ getPokemonName(this.$store.state.machines.currentPokemonId) }!`,
            duration: 3
        });
    },
    createBinaryString() {
        const flags = this.data.map(tm => tm.enabled ? 1 : 0)

        const machine1BinaryString = flags.slice(0, 32).reverse().join("");
        const machine2BinaryString = flags.slice(32, 64).reverse().join("");
        const machine3BinaryString = flags.slice(64, 96).reverse().join("");
        const machine4BinaryString = flags.slice(96, 100).reverse().join("");

        const machine1Decimal = bin2dec(machine1BinaryString);
        const machine2Decimal = bin2dec(machine2BinaryString);
        const machine3Decimal = bin2dec(machine3BinaryString);
        const machine4Decimal = bin2dec(machine4BinaryString);

        return {
            machine1: machine1Decimal,
            machine2: machine2Decimal,
            machine3: machine3Decimal,
            machine4: machine4Decimal,
        }
    }
  },
  watch: {
        getCurrentPokemonId() {
            const flags = convertToTMList(this.getTMListOfPokemon);
            for(let i = 0; i < this.data.length; i++) {
                this.data[i].enabled = flags[i] === 1;
            }
        }
    }
}
</script>