function dec2bin(dec) {
    return (dec >>> 0).toString(2);
}

function bin2dec(bin) {
    return parseInt(bin, 2);
}

function addMissingBits(bitArray) {
    if(bitArray.length < 32) {
        const missingBits = 32 - bitArray.length;
        for(let i = 0; i < missingBits; i++) {
            bitArray.unshift(0);
        }
    }

    return bitArray;
}

export {
    dec2bin,
    bin2dec,
    addMissingBits
}