<template>
     <ALayout>
        <AForm :model="formState" :label-col="labelCol" :wrapper-col="wrapperCol">
             <AFormItem label="Base HP">
                <AInputNumber :min="1" :max="999" v-model:value="formState.basic_hp" :step="1" :precision="0"/>
             </AFormItem>
            <AFormItem label="Base Attack">
                <AInputNumber :min="1" :max="999" v-model:value="formState.basic_atk" :step="1" :precision="0"/>
            </AFormItem>
            <AFormItem label="Base Defense">
                <AInputNumber :min="1" :max="999" v-model:value="formState.basic_def" :step="1" :precision="0"/>
            </AFormItem>
            <AFormItem label="Base Special Attack">
                <AInputNumber :min="1" :max="999" v-model:value="formState.basic_spatk" :step="1" :precision="0"/>
            </AFormItem>
            <AFormItem label="Base Special Defense">
                <AInputNumber :min="1" :max="999" v-model:value="formState.basic_spdef" :step="1" :precision="0"/>
            </AFormItem>
            <AFormItem label="Base Speed">
                <AInputNumber :min="1" :max="999" v-model:value="formState.basic_agi" :step="1" :precision="0"/>
            </AFormItem>
            <AFormItem label="Type 1">
                <ASelect :options="typeOptions" v-model:value="formState.type1" />
            </AFormItem>
            <AFormItem label="Type 2">
                <ASelect :options="typeOptions" v-model:value="formState.type2" />
            </AFormItem>
            <AFormItem label="Ability 1">
                <ASelect :options="abilityOptions" v-model:value="formState.tokusei1" />
            </AFormItem>
            <AFormItem label="Ability 2">
                <ASelect :options="abilityOptions" v-model:value="formState.tokusei2" />
            </AFormItem>
            <AFormItem label="Hidden Ability">
                <ASelect :options="abilityOptions" v-model:value="formState.tokusei3" />
            </AFormItem>
            <AFormItem label="Save Changes">
                <AButton type="primary" @click="saveChanges" :disabled="!this.getCurrentPokemonId">Save</AButton>
            </AFormItem>
         </AForm>
     </ALayout>
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import types from '../assets/json/types.json'
import abilities from '../assets/json/abilities.json'
import getPokemonName from '../utils/getPokemonName';
import { notification } from 'ant-design-vue';

function makeFirstLetterUppercase(string) {
    const strings = string.split(' ');
    let newString = '';
    strings.forEach(str => {
        newString += str.charAt(0).toUpperCase() + str.slice(1) + ' ';
    });

    return newString.trim();
}

const typeOptions = Object.entries(types).map(([key, value]) => ({
                value: Number(key),
                label: value
            }))
const abilityOptions = abilities.map((ability) => ({
                value: ability.value,
                label: makeFirstLetterUppercase(ability.label)
            }))

export default {
    data() {
        return {
            typeOptions,
            abilityOptions,
            formState: {
                basic_hp: 1,
                basic_atk: 1,
                basic_def: 1,
                basic_spatk: 1,
                basic_spdef: 1,
                basic_agi: 1,
                type1: typeOptions[0].value,
                type2: typeOptions[0].value,
                tokusei1: abilityOptions[0].value,
                tokusei2: abilityOptions[0].value,
                tokusei3: abilityOptions[0].value
            },
            labelCol: { span: 7 },
            wrapperCol: { span: 10 }
        }
    },
    computed: mapGetters('machines', ['getCurrentPokemon', 'getCurrentPokemonId']),
    methods: {
        ...mapActions('machines', ['setPokemonId', 'setCurrentPokemonData', 'setMutatedCurrentPokemonData', 'saveCurrentPokemonDataToArray', 'saveArrayToJson']),
        loadCurrentPokemonData() {
            this.formState.basic_hp = this.getCurrentPokemon.basic_hp;
            this.formState.basic_atk = this.getCurrentPokemon.basic_atk;
            this.formState.basic_def = this.getCurrentPokemon.basic_def;
            this.formState.basic_agi = this.getCurrentPokemon.basic_agi;
            this.formState.basic_spatk = this.getCurrentPokemon.basic_spatk;
            this.formState.basic_spdef = this.getCurrentPokemon.basic_spdef;

            this.formState.type1 = this.getCurrentPokemon.type1
            this.formState.type2 = this.getCurrentPokemon.type2;

            this.formState.tokusei1 = this.getCurrentPokemon.tokusei1;
            this.formState.tokusei2 = this.getCurrentPokemon.tokusei2;
            this.formState.tokusei3 = this.getCurrentPokemon.tokusei3;

        },
        saveChanges() {
            try {
                if(!this.getCurrentPokemonId) {
                    throw new Error('NO_ID');
                }
                this.setMutatedCurrentPokemonData(this.formState);
                this.saveCurrentPokemonDataToArray();
                this.saveArrayToJson();
                notification.success({
                    message: 'Success',
                    description: `${
                        getPokemonName(this.getCurrentPokemonId)
                    }'s data has been saved.`
                });
            } catch (e) {
                notification.error({
                    message: 'Error',
                    description: 'Something went wrong'
                });
            }
        }
    },
    mounted() {
        if(this.getCurrentPokemonId) {
            this.loadCurrentPokemonData();
        }
    },
    watch: {
        getCurrentPokemonId(newVal, oldVal) {
            if(newVal && newVal !== oldVal) {
                this.loadCurrentPokemonData();
            } else {
                notification.error({
                    message: 'Error',
                    description: 'No pokemon selected'
                });
            }
        }
    },
}
</script>